.messageModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-color: rgba(0,0,0,.4);
}

.messageModal-fragment {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    height: 150px;
    padding: 3rem;
    
    border-radius: 30px;

    box-shadow: 3px 4px 55px 0px rgba(0,0,0,0.75);

    background-color: #212429;
}

.messageModal-textarea {
    position: relative;
    width: 100%;
    height: 110px;

    padding: 1rem 0;
    padding-left: 1rem;
    margin-bottom: 25px;
    border-radius: 30px;
    background: none;
    outline: none;
    color: white;
    border: none;
}

.messageModal-inner {
    position: relative;
    text-align: center;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.commonButton {
    box-shadow: 3px 4px 55px 0px rgba(0,0,0,0.75);
    background: #212429;
    outline: none;
    border: none;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 30px;
    font-size: 14px;
    text-decoration: none;
  }
  
  .commonButton:hover {
    cursor: pointer;
    transition: 0.7s;
    color: yellow;
  }
  .containerModal-cancel:hover {
    cursor: pointer;
    transition: 0.7s;
    color: yellow;
  }